import { routes as ProductRouting } from './modules/product'
import { HomeComponent, PageNotFoundComponent } from './components'

export default [
  {
    path: '/home',
    component: HomeComponent,
    name: 'homeComponent'
  },
  ...ProductRouting,
  {
    path: '',
    redirect: '/home'
  },
  {
    path: '*',
    component: PageNotFoundComponent,
    name: 'pageNotFound'
  }
]



