<template>
  <div id="home">
    <!--Navbar-->
    <nav class="navbar navbar-transparent navbar-absolute fund-navbar">
      <div class="collapse navbar-collapse">
        <ul class="nav navbar-nav nav-center">
          <li>
            <a>Trending</a>
          </li>
          <li>
            <a>Hot Deals</a>
          </li>
          <li>
            <a>New Update</a>
          </li>
        </ul>
      </div>
    </nav>

    <!--Search Area-->
    <div class="main-content">
      <div class="container-fluid">

        <!--Search Input-->
        <div class="row">
          <div class="col-md-6 col-md-offset-3">
            <div class="input-group">
              <input class="form-control" placeholder="Search..." type="text" value="">
              <span class="input-group-addon" @click="onSearchBtnClick"><i class="fa fa-search"></i></span>
            </div>
          </div>
        </div>

        <h3 class="card-title text-center">Today's Trending</h3>

        <div class="row">
          <div class="col-lg-3" v-for="product in products" :key="product.id">
            <router-link :to="{ name: 'productDetail', params: {id: product.id}}">
              <div class="card">
              <div class="card-header text-center">
                <h5 class="card-title">{{product.name}}</h5>
              </div>
              <div class="card-content">
                <div class="row">
                  <img src="../../assets/images/iphone7.jpg"/>
                </div>
                <div class="card-footer">
                  <hr>
                  <p>{{product.provider}}</p>
                  <div class="footer-title">{{product.price}} VND</div>
                </div>
              </div>
            </div>
            </router-link>
          </div>
        </div>

        <h3 class="card-title text-center">Today's Dealers</h3>

        <div class="row">
          <div class="col-lg-3" v-for="product in products" :key="product.id">
            <router-link :to="{ name: 'productDetail', params: {id: product.id}}">
              <div class="card">
                <div class="card-header text-center">
                  <h5 class="card-title">{{product.name}}</h5>
                </div>
                <div class="card-content">
                  <div class="row">
                    <img src="../../assets/images/iphone7.jpg"/>
                  </div>
                  <div class="card-footer">
                    <hr>
                    <p>{{product.provider}}</p>
                    <div class="footer-title">{{product.price}} VND</div>
                  </div>
                </div>
              </div>
            </router-link>
          </div>
        </div>

        <h3 class="card-title text-center">Today's Products</h3>

        <div class="row">
          <div class="col-lg-3" v-for="product in products" :key="product.id">
            <router-link :to="{ name: 'productDetail', params: {id: product.id}}">
              <div class="card">
              <div class="card-header text-center">
                <h5 class="card-title">{{product.name}}</h5>
              </div>
              <div class="card-content">
                <div class="row">
                  <img src="../../assets/images/iphone7.jpg"/>
                </div>
                <div class="card-footer">
                  <hr>
                  <p>{{product.provider}}</p>
                  <div class="footer-title">{{product.price}} VND</div>
                </div>
              </div>
            </div>
            </router-link>
          </div>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
import { MOCK_PRODUCT_DATA } from '../mock-data/mock-product'
import { HTTP } from '../service/http-common'
export default {
  name: 'Home',

  data: () => {
    return {
      products: []
    }
  },

  methods: {
    onSearchBtnClick () {
      HTTP.get('products-select-top?rows=20')
        .then(res => console.log(res))
        .catch(err => console.log(err))
    }
  },

  created () {
    this.products = MOCK_PRODUCT_DATA
    HTTP.get('products-select-top?rows=10')
      .then(res => console.log(res))
      .catch(err => console.log(err))
  }
}
</script>

<style scoped>
  #home {}
  .input-group-addon :hover {
    font-size: 20px;
    cursor: pointer;
  }
</style>



