import { ProductDetailComponent, ProductListComponent } from './components'
import ProductComponent from './Product.vue'

export default [
  {
    path: '/product',
    component: ProductComponent,
    children: [
      {
        path: '',
        component: ProductDetailComponent,
        name: 'productList'
      },
      {
        path: ':id/detail',
        component: ProductListComponent,
        name: 'productDetail'
      }
    ]
  }
]



